/*-----stack------------------------------------*/

.skill {
    margin-top: 180px;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skill-title {
    padding: 10px;
    width: 800px;
    border-top:solid 1px #333;
    text-align: left;
    font-size: 28pt;
}

.skill-ul {
    margin-top: 60px;
 display: flex;
 width: 800px;
 flex-direction: column;
 gap: 35px;
}

.skill-li {
    list-style: none;
    display: block;
}

.skill-bar {
    display: flex;
    align-items: center;
    gap: 0px;
}

.skill-container {
    border-radius: 5px;
    width: 80%;
    height: 28px;
    background-color: #faf8f8;
    box-shadow: 5px 5px 5px #ccc;
}

.skill-gage {
    border-radius: 5px 15px 15px 5px;
    height: 28px;
    background: linear-gradient(to right, rgb(224, 247, 233), rgb(19, 223, 73));
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10px;
    color: #333;
    font-size: 10pt;
}

.my-skill {
    width: 100px;
}




